import { useState, useEffect } from 'react';
import { Link, useLocation, useHistory } from 'react-router-dom';
import { connect } from 'react-redux';
import Drawer from '@material-ui/core/Drawer';

import SearchBar from './SearchBar/SearchBar';
import CartButton from './CartButton/CartButton';
import DehazeButton from './DehazeButton/DehazeButton';

import logo from '../../assets/logo.png';
import Modal from '../../components/Modal/Modal';
import PopUpWindow from '../../components/PopUpWindow/PopUpWindow';

import SimpleButton from '../../components/SimpleButton/SimpleButton';
import LinkButton from '../../components/LinkButton/LinkButton';

import notRenderInCheckout from '../../utils/notRenderInCheckout';

import { userSignOut } from '../../actions';

import CategoryMenuPhone from '../CategoryMenuPhone/CategoryMenuPhone';
import useStyles from './style';

function Header(props) {
  const [showModal, setShowModal] = useState(false);
  const [open, setOpen] = useState(false);

  const location = useLocation();
  const history = useHistory();
  const classes = useStyles();

  //在換頁時關閉抽屜
  useEffect(() => {
    setOpen(false);
  }, [location.pathname, location.search]);

  return (
    <div className={classes.root}>
      <Link to='/'>
        <img className={classes.logo} src={logo} alt='LOGO' />
      </Link>
      {showModal ? (
        <Modal>
          <PopUpWindow showModal={showModal} setShowModal={setShowModal} />
        </Modal>
      ) : null}

      <Drawer
        className={classes.hideWhenSmUp}
        variant='persistent'
        anchor='left'
        open={open}>
        <CategoryMenuPhone />
      </Drawer>
      <SearchBar />

      {props.isLogin ? (
        <LinkButton
          onClick={() => {
            history.push('/');
            props.signOut();
          }}>
          登出
        </LinkButton>
      ) : (
        <SimpleButton
          className={classes.login}
          onClick={() => {
            setShowModal(!showModal);
          }}>
          註冊/登入
        </SimpleButton>
      )}
      <div className={classes.iconGroup}>
        <Link to='/cart'>
          <CartButton />
        </Link>
        {notRenderInCheckout(
          <DehazeButton
            className={classes.hideWhenSmUp}
            open={open}
            setOpen={setOpen}
          />
        )}
      </div>
    </div>
  );
}
const mapStateToProps = (state) => ({
  isLogin: state.isLogin,
});

const mapDispatchToProps = (dispatch) => ({
  signOut: () => {
    dispatch(userSignOut());
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(Header);
